<template lang="pug">
#main-slider.slide1-active
  button#main-slider-scroll {%fieldValue($welcome,0,0)%}
  .waves
    .filler
    .wave: icon.wave-1(name="wave-1", :includesize="false")
    .wave: icon.wave-2(name="wave-2", :includesize="false")
    .wave: icon.wave-bg(name="wave-bg", :includesize="false")
  .content
    .img
      .slide1-img #[.layer] #[.layer] #[.layer]
      .slide2-img #[.layer] #[.layer] #[.layer]
      .slide3-img #[.layer] #[.layer] #[.layer]
    .info: .wrap
      .slide1-text
        h1.title {%fieldValue($welcome,0,1)%}
        p.
          {%fieldValue($welcome,0,2)%}
      .slide2-text
        h1.title {%fieldValue($welcome,0,3)%}
        p.
          {%fieldValue($welcome,0,4)%}
      .slide3-text
        h1.title {%fieldValue($welcome,0,5)%}
        p.
          {%fieldValue($welcome,0,6)%}
      slider-navigation
</template>

<script>
import Icon from '@/helpers/Icon'
import SliderNavigation from '@/components/SliderNavigation'

export default {
  components: {
    Icon,
    SliderNavigation
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/style/helpers/utils'
@import '~@/style/helpers/adaptive'

@import '~@/style/layout'
@import '~@/style/effects'
@import '~@/style/colors'

on-small-tablet()
  @media only screen and (max-width: 1100px)
    {block}

#main-slider
  overflow hidden
  margin-top -100px
  height 100vh
  min-height 540px
  +respond-to(t)
    min-height 470px
  +respond-to(m)
    min-height 400px
    height auto
    margin-top 0
    padding 60px 0
  position relative
  overflow hidden
  background url('~@/assets/bg.png') #1a1920

.wrap
  position relative

.nav
  position absolute
  top 380px
  left 0
  force-gpu()
  opacity 0
  transform translateZ(0) translate(0, 100px)
  transition opacity .4s .3s, transform .6s .3s
  .loaded &
    transform translate(0, 0)
    opacity 1
  +respond-to(small)
    margin-top 0
    top 300px
  +respond-to(m)
    display inline-block
    top 350px
    right 0

#main-slider-scroll
  position absolute
  vertical-align middle
  bottom 20px
  left 50%
  margin-left -80px
  z-index 1
  color #fff
  font-size 13px
  text-transform uppercase
  padding 10px
  background none
  min-width 0
  transform translate(0, 100px)
  opacity 0
  transition transform .5s .3s, opacity .5s .3s
  .loaded &
    transform translate(0, 0)
    opacity 1
  +respond-to(m)
    display none
  &::before
    content ''
    display inline-block
    margin -10px 10px 0 0
    vertical-align middle
    width 17px
    height 33px
    background url('~@/assets/svg-for-css/scroll-ico.svg')
    animation scroll-ico 3s 2s linear infinite
  &:hover::before
    animation scroll-ico-fast 1s linear infinite

@keyframes scroll-ico
  0%
    transform translateY(0)
  10%
    transform translateY(-5px)
  20%
    transform translateY(5px)
  30%
    transform translateY(0)

@keyframes scroll-ico-fast
  0%
    transform translateY(0)
  33%
    transform translateY(-5px)
  66%
    transform translateY(5px)


.img
  force-gpu()
  float right
  position relative
  top 50%
  transition transform .3s
  +on-small-tablet()
    display none
  +respond-to(small)
    transform scale(.7)
    transform-origin 100% 50%
  +respond-to(pt-t)
    display block
    top 100%

slide-img(name, w, h, correction = 0, offset = 0)
  top (50 + h/-2) px
  left (w * -1) px
  if correction
    left (correction - w) px
  .loaded &
    $glitch(name, w, h)
  +respond-to(pt-t)
    top (-30 - offset - h) px

  .layer
    position absolute
    top 50%
    left 0
    background url('./assets/' + name+ '.png')
    +respond-to(w)
      background url('./assets/' + name + '-large.png')
    &:nth-child(2),
    &:nth-child(3)
      opacity 0
      clip rect(0, 0, 0, 0)
    width w px
    height h px

.waves
  force-gpu()
  +respond-to(m)
    display none
  position absolute
  left 0
  +respond-to(t)
    left 30%
  height 100%
  width 100%
  opacity 0
  transform translateZ(0) translate(150px, 0)
  transform-style preserve-3d
  backface-visibility hidden
  transition opacity .4s .3s, transform .4s .3s, left .2s
  .loaded &
    transform translate(0, 0)
    opacity 1

.filler
  position absolute
  left 50%
  height 100%
  width 50%
  margin-left 250px
  background $colors['accent'];

.wave
  position absolute
  top 60px
  left 50%
  height 100%
  transform translateX(-50%)

$wave
  fill $colors['accent'];
  height 100%
  force-gpu()

.wave-bg
  @extend $wave
  opacity 1
  transform translate3d(0,20px,0)
  animation waveBg 12s .1s linear infinite

.wave-1
  @extend $wave
  opacity .5
  animation wave 8s .1s linear infinite

.wave-2
  @extend $wave
  opacity .3
  animation wave 9s .1s linear infinite

@keyframes wave
  0%
    transform rotateZ(0deg) translate3d(0,30px,0) rotateZ(0deg)
  100%
    transform rotateZ(360deg) translate3d(0,30px,0) rotateZ(-360deg)

@keyframes waveBg
  0%
    transform rotateZ(0deg) translate3d(0,20px,0) rotateZ(0deg)
  100%
    transform rotateZ(360deg) translate3d(0,20px,0) rotateZ(-360deg)

.content
  @extend $wrap
  height 100%

.title
  color #fff
  font-weight 900
  line-height 1.33
  letter-spacing 2px
  +respond-to(small)
    margin 30px 0
    font-size 30px
  +respond-to(m)
    margin 0 0 30px
    font-size 25px

.info
  color #fff
  width 420px
  position absolute
  top 50%
  margin-top -200px
  +respond-to(t)
    margin-top -145px
  +respond-to(m)
    text-align center
    position relative
    width 100%
    top 0
    margin-top 0
  +respond-to(pt-t)
    margin-top -300px
    width 60%
    .wrap
      width 420px
      margin 0 auto

.slide1-img
  slide-img('helm', 838, 640, 90, 0)

.slide2-img
  slide-img('swissarmy', 689, 481, 0, 30)

.slide3-img
  slide-img('surface', 718, 424, 50, 60)

.slide1-img,
.slide2-img,
.slide3-img
  position absolute
  opacity 0
  transform translateX(200px)
  transition opacity .3s, transform .4s

.slide1-text,
.slide2-text,
.slide3-text
  display block
  z-index 3
  border none
  position absolute
  width 100%
  text-align left
  opacity 0
  transform translateX(-100px)
  transition opacity .3s, transform .4s
  +respond-to(m)
    transform translateX(0)
  h1, p
    color #fff
  h1
    transition transform .3s

a.slide1-text,
a.slide2-text,
a.slide3-text
  &:hover h1
    transform translateX(-15px)
    +respond-to(m)
      transform translateX(0)

$visible_slide
  transform translateX(0)
  z-index 4
  opacity 1
  transition opacity .4s .2s, transform .5s .2s

.loaded .slide1-active
  .slide1-img,
  .slide1-text
    @extend $visible_slide

.loaded .slide2-active
  .slide2-img,
  .slide2-text
    @extend $visible_slide

.loaded .slide3-active
  .slide3-img,
  .slide3-text
    @extend $visible_slide
</style>



