<template lang="pug">
.page
  .light-section: .content
    h1.title {%presValue($contacts,0)%}
    contact-info.light-bg
  .map-section
    map-box.map
</template>

<script>
import ContactInfo from '@/components/ContactInfo'
import MapBox from '@/components/Map'

export default {
  components: {
    ContactInfo,
    MapBox
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/style/helpers/adaptive'

@import '~@/style/layout'
@import '~@/style/sections'

.content
  @extend $wrap
  @extend $section
  clearfix()
  padding-bottom 120px
  +respond-to(m)
    padding-bottom 45px

.map-section
  @extends $dark-bg-section
  +respond-to(m)
    padding 0

.map
  margin 30px
  +respond-to(m)
    margin 0
</style>



