<template>
<svg :viewBox="svgImg.viewBox" :width="includesize?size.w:null" :height="includesize?size.h:null"><use :xlink:href="`#${svgImg.id}`"/></svg>
</template>

<script>
export default {
  props: {
    name: String,
    includesize: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    size () {
      var points = this.svgImg.viewBox.split(' ')
      return {
        w: points[2],
        h: points[3]
      }
    },
    svgImg () {
      return require(`@/assets/svg-sprite/${this.name}.svg`)
    }
  }
}
</script>



