<template lang="pug">
a.phone(href!="tel:{%short($company->d->companyMainPhone)%}") #[icon.ico(name="tel")] {%$company->d->companyMainPhone%}
</template>

<script>
import Icon from '@/helpers/Icon'

export default {
  components: {
    Icon
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/style/colors'

$ico
  display inline-block
  line-height 24px
  vertical-align middle
  margin -5px 10px 0 0

.phone
  font-size 13px
  cursor default
  padding 5px
  letter-spacing 1.2px
  color $colors['medium'];
  border-bottom none
  line-height 24px
  vertical-align middle
  transition margin .1s
  .ico
    @extends $ico
    fill $colors['medium'];
    width 17px
    height 24px
</style>



