<template lang="pug">
#app
  site-header
  mobile-menu
  form-panel
  side-bar
  router-view
  site-footer
</template>

<script>
import SiteHeader  from '@/components/SiteHeader'
import SiteFooter  from '@/components/SiteFooter'
import SideBar     from '@/components/SideBar'
import FormPanel   from '@/components/FormPanel'
import MobileMenu  from '@/components/MobileMenu'

export default {
  name: 'CyberSec',
  components: {
    SiteHeader,
    SiteFooter,
    SideBar,
    FormPanel,
    MobileMenu
  }
}
</script>

<style lang="stylus">
@import '~@/style/helpers/normalize'

@import '~@/style/font'
@import '~@/style/typography'

body
  &::before
    content ''
    display block
    height 0px
    height 100px
    opacity 0
  &::after
    content ''
    position fixed
    opacity 0
    visibility hidden
    pointer-events none
    background #fff
    top 0
    left 0
    right 0
    bottom 0
    z-index 10
    transition visibility .2s, opacity .2s

  &.mobile-menu-active,
  &.contact-form-active
    overflow hidden
    &::after
      pointer-events all
      opacity .8
      visibility visible

</style>



