<template lang="pug">
.slider
  script(type="images-list", class="images-list").
    {%fieldValue($about,0,4)%} {%fieldValue($about,0,5)%}
    {%fieldValue($about,0,6)%} {%fieldValue($about,0,7)%}
    {%fieldValue($about,0,8)%}
  .controls
    button.prev-slide: icon.ico(name="arrow-left-thin")
    .count #[span.count-curr] / #[span.count-all]
    button.next-slide: icon.ico(name="arrow-right-thin")
  .slide0
  .slide1
  .slide2
  .slide3
  .slide4
</template>

<script>
import Icon from '@/helpers/Icon'

export default {
  components: {
    Icon
  }
}
</script>

<style lang="stylus">
@import '~@/style/colors'

.slider
  display block
  max-width 545px
  position relative
  &::after
    content ''
    display block
    padding-bottom 100%
    margin-bottom 45px

  .slide0,
  .slide1,
  .slide2,
  .slide3,
  .slide4
    position absolute
    border-radius 10px
    box-shadow 0 6px 12px rgba(20, 13, 2, 0.53)
    margin 0
    font-size 0
    background #ccc
    width 100%
    &::before
      content ''
      display block
      padding-bottom 99%
    img
      border-radius 10px
      width 100%
      position absolute
      top 0
      left 0
  .slide0
    opacity 0
    pointer-events none
    visibility hidden
    z-index 1
    top -90px
    transform scale(1.2)
  .slide1
    z-index 4
    top 0
  .slide2
    pointer-events none
    z-index 3
    top 30px
    transform scale(.95)
  .slide3
    pointer-events none
    z-index 2
    top 60px
    transform scale(.90)
  .slide4
    opacity 0
    pointer-events none
    visibility hidden
    z-index 1
    top 30px
    transform scale(.85)

  &.next-slide
    .slide0,
    .slide1,
    .slide2,
    .slide3,
    .slide4
      transition opacity .4s, transform .4s, top .4s
    .slide1
      top -90px
      transform scale(1.2)
      opacity 0
    .slide2
      top 0px
      transform scale(1)
    .slide3
      top 30px
      transform scale(.95)
    .slide4
      opacity 1
      visibility visible
      transform scale(.90)
      top 60px
  &.prev-slide
    .slide0,
    .slide1,
    .slide2,
    .slide3,
    .slide4
      transition opacity .4s, transform .4s, top .4s
    .slide0
      opacity 1
      visibility visible
      top 0
      transform scale(1)
      z-index 4
    .slide1
      top 30px
      z-index 3
      transform scale(.95)
    .slide2
      top 60px
      z-index 2
      transform scale(.90)
    .slide3
      opacity 0
      z-index 1
      top 30px
      transform scale(.85)
    .slide4
      opacity 0.1
      top 0px
      transform scale(.80)
  .controls
    font-size 0
    position absolute
    bottom 20px
    left 50%
    margin-left -120px
    z-index 5
    button
      vertical-align middle
      min-width 0
      width 60px
      height 60px
      border-radius 100%
      padding 0
      background #fff
      opacity .5
      transition background-color .3s, opacity .3s
      .ico
        height 60px
        width 60px
        fill $colors['dark1'];
        transition fill .3s
      &:hover
        background-color $colors['dark1'];
        opacity 1
        .ico
          fill #fff
    .count
      text-shadow 0 0 5px #000
      display inline-block
      font-size 22px
      vertical-align middle
      text-align center
      width 120px
</style>



