<template lang="pug">
.nav
  a.arrow-left(href="#"): icon.ico(name="arrow-left")
  ul.dots
    li.dot-1: a(href="#")
    li.dot-2: a(href="#")
    li.dot-3: a(href="#")
  a.arrow-right(href="#"): icon.ico(name="arrow-right")
</template>

<script>
import Icon from '@/helpers/Icon'

export default {
  components: {
    Icon
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/style/colors'

$arrow
  border-bottom none
  opacity .3
  transition opacity .3s
  &:hover
    opacity 1
    .on-red & .ico
      fill #fff
  vertical-align middle
  padding 20px
  .ico
    fill #fff
    width 36px
    height 20px
    transition color .4s
    .on-red &
      fill #000

.nav
  vertical-align middle

.arrow-left
  @extend $arrow
  margin-left -20px

.arrow-right
  @extend $arrow
  margin-right -20px

.dots
  vertical-align middle
  list-style-type none
  display inline-block
  margin 0
  padding 0
  a
    border-bottom none
    padding 20px
    display block
    &::after
      opacity .3
      transition opacity .3s, background-color .3s, box-shadow .1s
      display block
      content ''
      border solid 3px #131219
      .on-red &
        border solid 3px $colors['accent'];
      border-radius 100%
      width 8px
      height 8px
      background-color #fff
      .on-red &
        background-color #000
    &:hover
      &::after
        opacity 1
        .on-red &
          background-color #fff

  li
    display inline-block
    .slide1-active &.dot-1,
    .slide2-active &.dot-2,
    .slide3-active &.dot-3
      a::after
        background-color #ff3b3b
        opacity 1
        box-shadow 0 0 0 1px #f93838
  .on-red & li
    .slide1-active &.dot-1,
    .slide2-active &.dot-2,
    .slide3-active &.dot-3
      a::after
        background-color #fff
        opacity 1
        box-shadow 0 0 0 1px #fff
</style>



