<template lang="pug">
section.contact-form#contact_form
  button#close_contact_form.close: icon.ico(name="close-cross")
  #form-container
    h3.title {%translate($company->d->contactUsTitleNames)%}
    form.form#contact_form_base
      .field#name_field
        input#name_input.input(name="name", required)
        label(for="name") {%translate($company->d->contactUsModal->fullNameTitles)%}
        .error_text
      .field#phone_field
        input#phone_input.input(name="phone", required)
        label(for="phone_input") {%translate($company->d->contactUsModal->contactPhoneTitles)%}
        .error_text
      .field#email_field
        input#email_input.input(name="email", required)
        label(for="email_input") {%translate($company->d->contactUsModal->emailTitles)%}
        .error_text
      .field#msg_field
        textarea#msg_input.input(name="msg", required)
        label(for="msg_input") {%translate($company->d->contactUsModal->messageTitles)%}
        .g-recaptcha(data-sitekey="6Lf1QycUAAAAAIqszWhvMSjfaSL0vu_5SMwQAVec")
        .error_text
      button#submit_contact_form.accent {%translate($company->d->contactUsModal->sendButtonTitle)%}
  #done-container: p.
    {%translate($company->d->contactUsModal->feedback)%}
</template>

<script>
import Icon from '@/helpers/Icon'

export default {
  components: {
    Icon
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/style/helpers/utils'
@import '~@/style/helpers/adaptive'

@import '~@/style/colors'

.contact-form
  position fixed
  overflow auto
  visibility hidden
  opacity 0
  pointer-events none
  top -100px
  padding-top 100px
  width 90%
  max-width 1400px
  right 0
  bottom 0
  z-index 11
  background-color #fff
  box-shadow 8px 0 30px
  transform translateX(400px)
  transition visibility .3s, opacity .3s, transform .4s
  .contact-form-active &
    visibility visible
    pointer-events all
    opacity 1
    transform translateX(0)
  +respond-to(m)
    width 100%
    box-shadow none

.close
  force-gpu()
  position absolute
  top 140px
  right 40px
  z-index 1
  transition transform .4s
  transform rotate(-180deg)
  .mobile-menu-active &
    transform rotate(0deg)
  background none
  min-width 0
  padding 30px
  .ico
    fill red
    width 32px
    height 32px
  +respond-to(m)
    top 108px
    right 10px

.title
  letter-spacing 2.2px
  width 80%
  margin 60px auto
  +respond-to(m)
    width auto
    font-size 24px
    margin 40px 20px

.field
  position relative
  label
    cursor text
    user-select none
    position absolute
    left 0
    top 0
    opacity 0.5
    color #161616
    letter-spacing 0.34px
    transform-origin 0 0
    transition transform .3s

.form
  width 700px
  margin 0 auto
  transition width .2s
  padding-bottom 40px
  transition transform .4s
  transform translateX(200px)
  .contact-form-active &
    transform translateX(0)
  +respond-to(t)
    width 70%
  +respond-to(m)
    width auto
    margin 0 20px

#submit_contact_form
  display block
  margin 40px auto 0
  transition transform .4s
  transform translateY(200px)
  .contact-form-active &
    transform translateY(0)
  +respond-to(m)
    margin 0 auto

#msg_input
  height 75px
  max-height 152px
  resize none

.input
  border 0
  border-bottom 1px solid #cfcfcf
  transition border-color .3s
  width 100%
  box-sizing border-box
  padding 5px 0
  font-size 24px
  margin-bottom 40px
  &:focus
    outline none
    border-color #8f92ff
  &:valid,
  &:focus
    & + label
      opacity 1
      transform translate3d(0, -16px, 0) scale(0.75)

.error_text
  position absolute
  font-size 12px
  top 40px
  color #fc1f2d
  opacity 0

.field.error
  .input
    border-bottom 1px solid #fc1f2d
  .error_text
    opacity 1
  label
    color #fc1f2d


#form-container
  z-index 2
  transition opacity .4s, visibility .4s
  &.hidden
    z-index 1
    visibility hidden
    opacity 0

#done-container
  position absolute
  top 100px
  left 0
  right 0
  bottom 0
  z-index 1
  pointer-events none
  visibility hidden
  opacity 0
  transition opacity .4s, visibility .4s
  p
    position absolute
    top 50%
    display block
    width 100%
    text-align center
    margin-top -86px
    transition transform .4s
    transform translateY(100px)
    &::before
      content ''
      background url('~@/assets/svg-for-css/thumbup.svg')
      display block
      margin 25px auto
      width 48px
      height 43px
  &.visible
    z-index 2
    visibility visible
    opacity 1
    p
      transform translateY(0)

</style>



