<template lang="pug">
.side
  | @isset($id)
  ul.languages
    | @foreach($languages as $language)
    | @if($id==='main')
    li: a(href!="{%route('main',['lang'=>$language->code])%}" class!="{%$language->printActiveness()%}")!="{%$language->name%}"
    | @else
    li: a(href!="{%route('page',['id'=>$id,'lang'=>$language->code])%}" class!="{%$language->printActiveness()%}")!="{%$language->name%}"
    | @endif
    | @endforeach
  | @endisset
  ul.soc
    | @if($company->hasSocialLink('instagram'))
    li: a(href!="{%$company->getSocialLink('instagram')%}", target="_blank"): icon.ico(name="instagram-icon")
    | @endif @if($company->hasSocialLink('telegram'))
    li: a(href!="{%$company->getSocialLink('telegram')%}", target="_blank"): icon.ico(name="telegram-icon")
    | @endif @if($company->hasSocialLink('facebook'))
    li: a(href!="{%$company->getSocialLink('facebook')%}", target="_blank"): icon.ico(name="facebook-icon")
    | @endif @if($company->hasSocialLink('vk'))
    li: a(href!="{%$company->getSocialLink('vk')%}", target="_blank"): icon.ico(name="vk-icon")
    | @endif @if($company->hasSocialLink('twitter'))
    li: a(href!="{%$company->getSocialLink('twitter')%}", target="_blank"): icon.ico(name="twitter-icon")
    | @endif @if($company->hasSocialLink('youtube'))
    li: a(href!="{%$company->getSocialLink('youtube')%}", target="_blank"): icon.ico(name="youtube-icon")
    | @endif
</template>

<script>
import Icon from '@/helpers/Icon'

export default {
  components: {
    Icon
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/style/helpers/utils'

on-small-tablet()
  @media only screen and (max-width: 1100px)
    {block}

.side
  force-gpu()
  width 70px
  padding 120px 10px 0
  z-index 9
  position fixed
  top 0
  left 0
  bottom 0
  text-align center
  background rgba(0, 0, 0, .2)
  color #fff
  .main-page &
    transform translateX(-100px)
  .loaded &
    margin-top 0
    transform translateX(0)
  transition background .3s, transform .5s .2s
  &:hover
    background rgba(0, 0, 0, .3)
  +on-small-tablet()
    display none

.languages, .soc
  margin 0
  padding 0
  list-style-type none

.languages
  margin-bottom 20px
  a
    position relative
    color #fff
    opacity .6
    text-transform uppercase
    font-size 10px
    transition opacity .3s
    padding 10px
    display inline-block
    border-bottom none
    &::before
      content ''
      position absolute
      bottom 8px
      left 10px
      right 10px
      height 2px
      opacity 0
      background-color #fff
      transform-origin right top
      transform scaleX(0) translate(-100px, 0)
      transition opacity .5s, transform 0.3s linear
    &:hover, &.active
      opacity 1
      &::before
        transform-origin left top
        opacity 1
        transform scaleX(1) translate(0, 0)
        transition opacity .4s, transform .3s cubic-bezier(0.165, 0.84, 0.44, 1)
    &:hover, &.active
      opacity 1
    &.active
      font-weight 900
      text-shadow 0 0 1px
.soc
  a
    force-gpu()
    border-bottom none
    display block
    padding 15px 10px 10px
    opacity .5
    transform-origin 50% 50%
    transition transform .3s cubic-bezier(0.165, 0.84, 0.44, 1), opacity .3s
    &:hover
      transform scale(2)
      opacity 1
  .ico
    fill #fff
    border-radius 2px
    width 18px
    height 18px
</style>



