<template lang="pug">
.container(:class="{'with-info': info}")
  #map-box
    a.button.accent(href!="{%fieldValue($contacts,1,0)%}", target="_blank") {%fieldValue($contacts,1,1)%}
  contact-info.panel(v-if="info")
</template>

<script>
import ContactInfo from '@/components/ContactInfo'

export default {
  components: {
    ContactInfo
  },
  props: {
    info: {
      type: Boolean,
      default: false
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/style/layout'
@import '~@/style/sections'

on-small-tablet()
  @media only screen and (max-width: 800px)
    {block}

.container
  height 450px
  background #ccc
  overflow hidden
  border-radius 10px
  position relative
  z-index 1
  +on-small-tablet()
    height auto
    border-radius 0

#map-box
  position absolute !important
  top 0
  left 0
  right 0
  .with-info &
    right 350px
  bottom 0
  +on-small-tablet()
    position relative !important
    right auto
    height 250px
  .button
    position absolute
    z-index 1000
    left 50%
    margin-left -100px
    bottom 30px

.panel
  position absolute
  top 0
  right 0
  bottom 0
  width 350px
  background #ff3b3b
  box-sizing border-box
  padding 40px
  display flex
  flex-direction: column
  justify-content space-around
  &::before
    content ''
    position absolute
    background url('~@/assets/map-panel-side.png')
    left -89px
    top -15px
    width 90px
    height 478px
    z-index 1000
  +on-small-tablet()
    position relative
    width auto
    padding 30px
</style>



