<template lang="pug">
section.menu
  .navigation
    button#close_mobile_menu.close: icon.ico(name="close-cross")
    ul.list
      | @foreach($pages as $page)
      li: a(href!="{%route('page',['id'=>$page->printId(),'lang'=>App::getLocale()])%}" class!="{%$page->printActiveness($id)%}")!='{%$page->printName()%}'
      | @endforeach
  footer.info
    phone
  | @isset($id)
  ul.languages
    | @foreach($languages as $language)
    li: a(href!="{%route('page',['id'=>$id,'lang'=>$language->code])%}" class!="{%$language->printActiveness()%}")!="{%$language->name%}"
    | @endforeach
  | @endisset
</template>

<script>
import Icon from '@/helpers/Icon'
import Phone from '@/components/Phone'

export default {
  components: {
    Icon,
    Phone
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/style/helpers/utils'
@import '~@/style/helpers/adaptive'
@import '~@/style/colors'

.menu
  position fixed
  overflow auto
  visibility hidden
  opacity 0
  pointer-events none
  top -100px
  padding-top 100px
  left 0
  width 400px
  bottom 0
  z-index 11
  background-color #fff
  box-shadow -8px 0 30px
  transform translateX(-400px)
  transition visibility .3s, opacity .3s, transform .4s
  +respond-to(m)
    box-shadow none
    width auto
    right 0
  .mobile-menu-active &
    transform translateX(0)
    visibility visible
    pointer-events all
    opacity 1
    +respond-to(m)
      opacity 0.96
.close
  force-gpu()
  transition transform .4s
  transform rotate(-180deg)
  .mobile-menu-active &
    transform rotate(0deg)
  background none
  min-width 0
  padding 30px
  .ico
    fill red
    width 32px
    height 32px
.list
  margin 0
  padding 0 0 20px
  list-style-type none
  a
    position relative
    padding 20px
    display block
    color $colors['base'];
    font-size 24px
    letter-spacing 2.22px
    text-transform uppercase
    border-bottom none
    transition padding .4s
    &::before
      force-gpu()
      content ''
      position absolute
      left 0
      top 50%
      margin-top -1px
      height 2px
      width 47px
      background #000
      transform translateX(-50px)
      transition transform .4s cubic-bezier(0.165, 0.84, 0.44, 1)
    &:hover, &.active
      padding-left 60px
      &::before
        transform translateX(0)

.navigation
  force-gpu()
  transition transform .4s
  transform translateX(-200px)
  .mobile-menu-active &
    transform translateX(0)
  min-height 100%
  margin-bottom -140px
  &::after
    content ''
    display block
    height 140px

.info
  height 100px
  padding 20px
  overflow hidden

.phone
  force-gpu()
  transition transform .4s
  transform translateY(200px)
  .mobile-menu-active &
    transform translateY(0)
  display inline-block
  margin-bottom 25px
  font-size 14px

.languages
  force-gpu()
  transition transform .4s
  transform translateY(200px)
  .mobile-menu-active &
    transform translateY(0)
  margin 0
  padding 0
  list-style-type none
  li
    display inline-block
    margin-right 20px
  a
    color $colors['dark1'];
    opacity .3
    text-transform uppercase
    border-bottom none
    font-size 14px
    display block
    padding 5px
    transition opacity .3s
    &:hover
      opacity 1
    &.active
      opacity 1
      color $colors['accent'];
</style>



