<template lang="pug">
.w
  slider.hero-slider
  .page
    #blockquotes.delayed-appear.slide1-active: .content
      // In the spacer block placed the longest quote text
      .spacer.
        {%fieldValue($welcome,1,1)%}
      .slide1
        .img: img(src!="{%fieldValue($welcome,1,0)%}")
        .text
          blockquote.
            {%fieldValue($welcome,1,1)%}
          .author
            | {%fieldValue($welcome,1,6)%}
            b {%fieldValue($welcome,1,7)%}
      .slide2
        .img: img(src!="{%fieldValue($welcome,1,2)%}")
        .text
          blockquote.
            {%fieldValue($welcome,1,3)%}
          .author
            | {%fieldValue($welcome,1,6)%}
            b {%fieldValue($welcome,1,7)%}
      .slide3
        .img: img(src!="{%fieldValue($welcome,1,4)%}")
        .text
          blockquote.
            {%fieldValue($welcome,1,5)%}
          .author
            | {%fieldValue($welcome,1,6)%}
            b {%fieldValue($welcome,1,7)%}
      slider-navigation.on-red

    services-section#section2.delayed-appear(type="section")
    clients-section#section3.delayed-appear
    #section4.delayed-appear.news-section: .content
      h2.title {%presValue($welcome,4)%}
      .cols
        | @foreach($main as $post)
        .col: .plate
          h3.link: a(href!="{%route('post',['lang'=>App::getLocale(),'pid'=>newsFieldValue($post,6)])%}") {%newsFieldValue($post,2)%}
          p(data-first-letter!="{%firstLetter($post,3)%}").
            {%newsFieldValue($post,3)%}
          a.more(href="{%route('post',['lang'=>App::getLocale(),'pid'=>newsFieldValue($post,6)])%}") {%fieldValue($news,0,2)%}
        | @endforeach
      a(href!="{%route('page',['id'=>'press-center','lang'=>App::getLocale()])%}").button.accent @lang("Еще новости")

    partners-section#section5.delayed-appear
    #section6.delayed-appear.map-section: .content
      h2.title {%presValue($welcome,6)%}
      map-component(:info="true").map

</template>

<script>
import Slider from '@/components/MainPageSlider'
import ClientsSection from '@/components/ClientsSection'
import PartnersSection from '@/components/PartnersSection'
import ServicesSection from '@/components/ServicesSection'
import SliderNavigation from '@/components/SliderNavigation'
import MapComponent from '@/components/Map'

export default {
  components: {
    Slider,
    ClientsSection,
    PartnersSection,
    ServicesSection,
    SliderNavigation,
    MapComponent
  },
  created () {
    document.body.classList.add('main-page')
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/style/helpers/utils'

@import '~@/style/layout'
@import '~@/style/sections'

on-tablet()
  @media only screen and (max-width: 1200px)
    {block}

on-mobile()
  @media only screen and (max-width: 640px)
    {block}

on-retina()
  @media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi)
    {block}

.content
  @extend $wrap

$cite-slide-active
  .text
    opacity 1
    transition opacity .4s .2s
    +respond-to(small)
      transition opacity .2s
  .img
    opacity 1

#blockquotes
  +respond-to(lap)
    &.delayed-appear .content
      transition transform .5s, opacity .5s
      transform scale(.8) translateY(80px)
      opacity 0
    &.delayed-appear.appear .content
      transform scale(1) translateY(0)
      opacity 1
  @extend $red-section
  background $colors['accent'];
  position relative
  padding 180px 0 80px
  +on-mobile()
    padding 35px 0 10px
  &::after
    content ''
    background url('~@/assets/waves.png') center bottom repeat
    +on-retina()
      background url('~@/assets/waves-x2.png') center bottom repeat
    position absolute
    height 65px
    bottom -64px
    left 0
    right 0
  .content
    position relative
    margin-bottom 120px
    min-height 335px
    +on-tablet()
      margin-bottom 0
    &::after,
    &::before
      content ''
      position absolute
      background url('~@/assets/svg-for-css/dots.svg')
      width 309px
      height 159px
      position absolute
      opacity .3
      +on-tablet()
        width 225px
        height 116px
        background-size contain
      +on-mobile()
        display none
    &::before
      transform rotate(180deg)
      top -147px
      left 15px
      +on-tablet()
        top -127px
    &::after
      top 360px
      left 15px
      +on-tablet()
        top 270px
  .slide1,
  .slide2,
  .slide3
    position absolute
    top 0
    left 0
  .img
    float left
    box-shadow 0 18px 64px -20px rgba(38, 45, 8, 0.7)
    overflow hidden
    border-radius 8px
    font-size 0
    width 340px
    +on-tablet()
      width 250px
    +on-mobile()
      width 100px
      float none
      margin-bottom 30px
    img
      width 100%
  .spacer,
  .text
    padding-left 420px
    padding-bottom 110px
    +on-tablet()
      padding-left 290px
    +on-mobile()
      padding-left 0
  .spacer
    opacity 0
    padding-top 1em
    z-index -1
    user-select none
    +on-mobile()
      padding-bottom 200px
  .nav
    position absolute
    bottom 0
    right 0
    +on-tablet()
      right auto
      left 290px
    +on-mobile()
      left 0
      right 0
      display inline-block
      text-align center
  .author
    display block
    position absolute
    bottom 18px
    font-size 15px
    width 240px
    +on-tablet()
      bottom auto
      left 30px
      top 344px
    +on-mobile()
      top 16px
      right 0
      left 110px
      width auto
      font-size 14px
    b
      display block
      font-family 'AdineuePro'
      font-size 24px
      font-weight 900
      text-transform uppercase
      letter-spacing 2px
      +on-mobile()
        font-size 18px
  .slide1
  .slide2
  .slide3
    .text
      opacity 0
      transition opacity .4s
    .img
      opacity 0
      transition opacity .5s

  &.slide1-active
    .slide1
      @extend $cite-slide-active


  &.slide2-active
    .slide2
      @extend $cite-slide-active

  &.slide3-active
    .slide3
      @extend $cite-slide-active

.news-section
  +respond-to(lap)
    &.delayed-appear .content
      transition transform .5s, opacity .5s
      transform translateX(160px)
      opacity 0
    &.delayed-appear.appear .content
      transform translateY(0)
      opacity 1
  overflow hidden
  @extends $section
  @extends $padded-section
  .cols
    clearfix()
    margin 0 -30px
  .col
    float left
    width 33%
    +respond-to(t)
      &:last-child
        display none
      width 50%
    +respond-to(m)
      width 100%
  .plate
    margin 0 30px 20px
  .button
    display table
    margin 80px auto 0
    +respond-to(m)
      margin 30px auto 20px

.map-section
  +respond-to(lap)
    &.delayed-appear .content
      transition transform .5s, opacity .5s
      transform scale(.8) translateY(80px)
      opacity 0
    &.delayed-appear.appear .content
      transform scale(1) translateY(0)
      opacity 1
  @extends $dark-bg-section
  padding-bottom 60px
  +respond-to(m)
    padding-bottom 0
    .map
      margin 0 -20px
</style>



