<template lang="pug">
section.section(:class="{block: type=='section'}"): .content
  h2.title {%presValue($services,0)%}
  ul.list
    li.plate: a(href!="{%route('service',['lang'=>App::getLocale(),'index'=>fieldValue($services,0,19)])%}")
      img(src="~@/assets/services-ico/1_w.png")
      span.text {%fieldValue($services,0,0)%}
    li.plate: a(href!="{%route('service',['lang'=>App::getLocale(),'index'=>fieldValue($services,0,20)])%}")
      img(src="~@/assets/services-ico/2_w.png")
      span.text {%fieldValue($services,0,2)%}
    li.plate: a(href!="{%route('service',['lang'=>App::getLocale(),'index'=>fieldValue($services,0,21)])%}")
      img(src="~@/assets/services-ico/3_w.png")
      span.text {%fieldValue($services,0,4)%}
    li.plate: a(href!="{%route('service',['lang'=>App::getLocale(),'index'=>fieldValue($services,0,22)])%}")
      img(src="~@/assets/services-ico/4_w.png")
      span.text {%fieldValue($services,0,6)%}
    template(v-if="type == 'page'")
      li.plate: a(href!="{%route('service',['lang'=>App::getLocale(),'index'=>fieldValue($services,0,23)])%}")
        img(src="~@/assets/services-ico/5_w.png")
        span.text {%fieldValue($services,0,8)%}
      li.plate: a(href!="{%route('service',['lang'=>App::getLocale(),'index'=>fieldValue($services,0,24)])%}")
        img(src="~@/assets/services-ico/6_w.png")
        span.text {%fieldValue($services,0,10)%}
      li.plate: a(href!="{%route('service',['lang'=>App::getLocale(),'index'=>fieldValue($services,0,25)])%}")
        img(src="~@/assets/services-ico/7_w.png")
        span.text {%fieldValue($services,0,12)%}
      li.plate: a(href!="{%route('service',['lang'=>App::getLocale(),'index'=>fieldValue($services,0,26)])%}")
        img(src="~@/assets/services-ico/8_w.png")
        span.text {%fieldValue($services,0,14)%}

  a(href!="{%route('page',['id'=>'services','lang'=>App::getLocale()])%}").button.accent(v-if="type == 'section'") {%fieldValue($services,0,16)%}
</template>

<script>
export default {
  props: {
    type: String
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/style/helpers/adaptive'
@import '~@/style/layout'
@import '~@/style/sections'

.content
  @extend $wrap

.section
  +respond-to(lap)
    &.delayed-appear .content
      transition transform .5s, opacity .5s
      transform scale(.8) translateY(80px)
      opacity 0
    &.delayed-appear.appear .content
      transform scale(1) translateY(0)
      opacity 1
  @extend $dark-bg-section
  &.block
    @extend $padded-section
    padding-top 150px

$SERVICES_PLATE_MARGIN = 30

.list
  padding 0
  margin 20px ($SERVICES_PLATE_MARGIN * -1 - 10)px
  list-style-type none
  text-align center
  +respond-to(m)
    margin 20px 0

.plate
  display inline-block
  vertical-align top
  margin 0 ($SERVICES_PLATE_MARGIN)px
  width 220px
  +respond-to(m)
    width 100%
    margin 0 0 30px

  a
    border-bottom none
    color #fff
    font-size 13px
    font-weight 700
    line-height 1.69
    text-transform uppercase
    letter-spacing 0.33px
    &:visited
      color #fff

  img
    display block
    width 100%
    transform scale(.95)
    transition transform .2s
    +respond-to(m)
      transform scale(1)
      float left
      width 100px

  a:hover img
    transform scale(1)

  .text
    display block
    margin 15px 10px 70px
    +respond-to(m)
      height 100px
      margin 0
      padding-left 15px
      text-align left
      display flex
      align-items center

.button
  display table
  margin 0 auto 20px
</style>



