<template lang="pug">
.page: .content
  h1.title {%presValue($news,0)%}
  ul.tabs
    li: a#press-main-tab-link.active(href="#") {%fieldValue($news,0,0)%}
    li: a#press-smi-tab-link(href="#") {%fieldValue($news,0,1)%}
  .news-main
    | @foreach($main as $post)
    .plate
      | @if($post->d->fields[0]->value!=='')
      a.preview(href="{%route('post',['lang'=>App::getLocale(),'pid'=>newsFieldValue($post,6)])%}"): img(style="box-shadow: 0 16px 30px hsla(340,2%,51%,.6);", src!="{%newsFieldValue($post,0)%}")
      | @endif
      h3.link: a(href!="{%route('post',['lang'=>App::getLocale(),'pid'=>newsFieldValue($post,6)])%}") {%newsFieldValue($post,2)%}
      p(data-first-letter!="{%firstLetter($post,3)%}").
        {%newsFieldValue($post,3)%}
      a.more(href="{%route('post',['lang'=>App::getLocale(),'pid'=>newsFieldValue($post,6)])%}") {%fieldValue($news,0,2)%}
    | @endforeach
    main-pagination
  aside.news-smi
    h2.side-title {%fieldValue($news,0,1)%}
    | @foreach($smi as $post)
    .plate
      | @if($post->d->fields[0]->value!=='')
      a.preview(href!="{%route('post',['lang'=>App::getLocale(),'pid'=>newsFieldValue($post,6)])%}"): img(style="box-shadow: 0 16px 30px hsla(340,2%,51%,.6);", src!="{%newsFieldValue($post,0)%}")
      | @endif
      h3.link: a(href!="{%route('post',['lang'=>App::getLocale(),'pid'=>newsFieldValue($post,6)])%}") {%newsFieldValue($post,2)%}
      p(data-first-letter!="{%firstLetter($post,3)%}").
        {%newsFieldValue($post,3)%}
      a.more(href="{%route('post',['lang'=>App::getLocale(),'pid'=>newsFieldValue($post,6)])%}") {%fieldValue($news,0,2)%}
    | @endforeach
</template>

<script>
import MainPagination from '@/components/MainPagination'
export default {
  components: {
    MainPagination
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/style/helpers/utils'
@import '~@/style/helpers/adaptive'

@import '~@/style/layout'
@import '~@/style/sections'

.content
  @extend $wrap
  @extend $section
  clearfix()
  padding-bottom 120px
  +respond-to(m)
    padding-bottom 45px

.plate
  padding 25px 0

.news-main
  margin-top -25px
  max-width 570px
  min-width 540px
  float left
  +respond-to(m)
    min-width 0
    width 100%
  +respond-to(small)
    margin auto
    float none
    .press-smi-active &
      display none
  .plate
    border-bottom solid 1px #ececec

.news-smi
  width 480px
  float right
  border-radius 3px
  box-sizing border-box
  padding 25px 120px 25px 50px
  background-color #f5f5f5
  .side-title
    font-size 17px
  +respond-to(small)
    max-width 570px
    min-width 540px
    width 100%
    margin auto
    float none
    padding 0
    background none
    .press-main-active &
      display none
    .side-title
      display none
  +respond-to(m)
    min-width 0
    width 100%
  .plate
    margin-right -120px
    padding-right 120px
    border-top solid 1px #dbdbdb
    +respond-to(small)
      margin-right 0
      padding-right 0
      border-top 0
      border-bottom solid 1px #ececec

.preview
  display block
  border none
  margin-bottom 25px
  +respond-to(m)
    margin-bottom 15px
  img
    width 100%
    border-radius 3px
    +respond-to(m)
      box-shadow none!important

.tabs
  display none
  +respond-to(small)
    display block
  +respond-to(m)
    margin-left -10px
  max-width 580px
  min-width 550px
  margin 0 auto 30px
  padding 0
  list-style-type none
  li
    display inline-block
  a
    user-select none
    padding 10px
    border-bottom none
    text-transform uppercase
    opacity .5
    font-size 13px
    transition opacity .3s
    color #000
    margin-right 20px
    &::before
      content ''
      position absolute
      bottom 8px
      left 10px
      right 10px
      height 2px
      opacity 0
      background-color #000
      transform-origin right top
      transform scaleX(0) translate(-100px, 0)
      transition opacity .5s, transform 0.3s linear
    &.active
      font-weight 900
    &:hover, &.active
      opacity 1
      &::before
        transform-origin left top
        opacity 1
        transform scaleX(1) translate(0, 0)
        transition opacity .4s, transform .3s cubic-bezier(0.165, 0.84, 0.44, 1)
</style>



