<template lang="pug">
header.site-header: .wrap
  a.logo(href="/") #[.img] #[icon.text(name="logo-text")]
  .info
    button#open_contact_form.send-msg-button #[icon.ico(name="send-msg-ico")] #[span.text {%translate($company->d->contactUsLinkNames)%}]
    phone
  nav.navigation
    h3#open_mobile_menu.hamburger: span.hamburger-text #[icon.hamburger-ico(name="hamburger")] @lang("Меню")
    ul.list
      | @foreach($pages as $page)
      li: a(href!="{%route('page',['id'=>$page->printId(),'lang'=>App::getLocale()])%}" class!="{%$page->printActiveness($id)%}")!='{%$page->printName()%}'
      | @endforeach
</template>

<script>
import Icon from '@/helpers/Icon'
import Phone from '@/components/Phone'

export default {
  components: {
    Icon,
    Phone
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/style/helpers/utils'
@import '~@/style/helpers/adaptive'
@import '~@/style/colors'

on-small-tablet()
  @media only screen and (max-width: 1100px)
    {block}
on-very-small-tablet()
  @media only screen and (max-width: 700px)
    {block}

$ico
  display inline-block
  line-height 24px
  vertical-align middle
  margin -5px 10px 0 0

.site-header
  force-gpu()
  clearfix()
  height 100px
  min-width 320px
  background #fff
  box-shadow 0 1px 2px rgba(0, 0, 0, 0.1)
  font-size 13px
  width 100%
  z-index 10
  position fixed
  top 0
  .main-page &
    margin-top -100px
    +respond-to(m)
      margin-top 0
  .loaded &
    margin-top 0
  transition top .2s, margin-top .6s .1s
  .site-header-invisible &
    pointer-events none
    top -95px

.wrap
  height 100%
  max-width 1600px
  margin 0 auto
  text-align center
  +respond-to(m)
    display flex

.logo
  display inline-block
  float left
  +on-small-tablet()
    float none
  height 100%
  border-bottom none
  padding 0 40px
  +respond-to(t)
    padding 0 26px
  +respond-to(m)
    padding 0
    order 2
    flex 1
  border-right solid 1px #f1f1f1
  +on-small-tablet()
    border-right none
  transition background-color .2s
  &::before
    content ''
    display inline-block
    height 100%
    width 1px
    vertical-align middle
  .img
    background url('~@/assets/svg-for-css/logo.svg')
    display inline-block
    width 80px
    height 80px
    vertical-align middle
    margin-right 10px
    +respond-to(t)
      margin-right 0
  .text
    display inline-block
    width 0
    opacity 0
    vertical-align middle
    transition fill .4s, width .2s, opacity .1s
    +respond-to(lap)
      width 96px
      opacity 1
a.logo:hover
  background #171717
  .text
    fill #fff

.navigation
  height 100%
  transition width .2s
  white-space nowrap
  +respond-to(m)
    order 1
  &::before
    content ''
    display inline-block
    height 100%
    width 1px
    vertical-align middle
  .hamburger
    display none
    user-select none
    width 50px
    cursor pointer
    border-right solid 1px #f1f1f1
    transition background .2s, color .4s
    &:hover
      color #fff
      background #171717
      .hamburger-ico
        fill #fff
  .hamburger-text
    display inline-block
    vertical-align middle
    font-family Lato
    font-size 13px
    font-weight 400
  .hamburger-ico
    display block
    margin 0 auto 10px
    height 12px
    transition fill .4s
  +on-small-tablet()
    float left
    &::before
      display none
    .hamburger
      padding 0 20px
      margin 0
      height 100%
      display inline-block
      &::before
        content ''
        display inline-block
        height 100%
        width 1px
        vertical-align middle
  .list
    display inline
    vertical-align middle
    list-style-type none
    margin 0
    padding 0
    +on-small-tablet()
      display none
  li
    display inline-block
    margin-left 20px
    &:first-child
      margin-left 0
  a
    position relative
    letter-spacing 1.2px
    color #000
    opacity .5
    cursor pointer
    border-bottom none
    transition opacity .4s
    padding 10px
    text-transform uppercase
    &::before
      content ''
      position absolute
      bottom 8px
      left 10px
      right 10px
      height 2px
      opacity 0
      background-color #000
      transform-origin right top
      transform scaleX(0) translate(-100px, 0)
      transition opacity .5s, transform 0.3s linear
    &:hover, &.active
      opacity 1
      &::before
        transform-origin left top
        opacity 1
        transform scaleX(1) translate(0, 0)
        transition opacity .4s, transform .3s cubic-bezier(0.165, 0.84, 0.44, 1)

.info
  float right
  height 100%
  +respond-to(m)
    order 3
.send-msg-button
  height 100%
  padding 0 35px
  overflow hidden
  +respond-to(t)
    padding 0 27px
  text-transform uppercase
  color #ff383a
  background none
  letter-spacing 1.2px
  border-left solid 1px #f1f1f1
  border-right solid 1px #f1f1f1
  +respond-to(m)
    border-right none
  line-height 24px
  vertical-align middle
  min-width 0
  white-space nowrap
  .text
    display inline-block
    transition width .2s, opacity .1s
    width 148px
  +on-very-small-tablet()
    padding 0 20px
    width 50px
    .text
      width 0
      opacity 0
  .ico
    @extends $ico
    fill #ff383a
    width 25px
    height 26px
    transition fill .4s
    +on-very-small-tablet()
      margin -5px -10px 0 0
  &:hover
    color #fff
    background #171717
    .ico
      fill #fff
.phone
  margin 0 28px
  +respond-to(t)
    margin 0 23px
  +respond-to(d)
    margin-left 120px
  +respond-to(m)
    display none
</style>



